<template>
    <div class="app-checkbox">
        <input 
            type="checkbox"
            :id="idFor"
            :value="name" 
            @input="updateSelf($event.target.value)" >
        <label :for="idFor">{{ label }}</label>
    </div>
</template>

<script>
export default {
    model: {
        prop: "name",
        event: "input"
    },
    props: {
        idFor: { required: true },
        label: { required: true },
        name: { required: true }
    },
    methods: {
        updateSelf(name) {
            this.$emit("input", name);
        }
    }

}
</script>

