<template>
    <div :class="`cell medium-${column}`">
        <label v-if="label">{{label}}</label>
        <div class="slider">
            <div class="slider__wrapper">
            <div v-if="!hideLabel" :style="{ left: position }" class="slider__label">{{ sliderLabel }}</div>
            <div class="slider__track" :class="{'slider__track--rectangular': !raising}">
                <div
                v-if="raising"
                :style="{ 'border-left-width': sliderWidth + 'px' }"
                class="slider__track-top"
                />
                <div
                v-if="raising"
                :style="{ 'border-right-width': sliderWidth + 'px' }"
                class="slider__track-bottom"
                />
            </div>
            <input
                ref="slider"
                v-model="sliderValue"
                :max="sliderMax"
                class="slider__input"
                type="range"
                :min="sliderMin"
                :step="step"
                @input="update"
                @change="change"
            />
            </div>
        </div>
        <slot />
    </div>
</template>

<script>
export default {
  props: {
    value: {
      type: String,
      required: false,
      default: ""
    },
    values: {
      type: Array,
      required: false,
      default: () => []
    },
    min: {
      type: String,
      required: false,
      default: "0"
    },
    max: {
      type: String,
      required: false,
      default: "300"
    },
    step: {
      type: String,
      required: false,
      default: "1"
    },
    hideLabel: {
      type: Boolean,
      required: false,
      default: false
    },
    raising: {
      type: Boolean,
      required: false,
      default: false
    },
    column: {
        type: String,
        required: true
    },
    label: {
        type: String,
        required: false
    }
  },
  data() {
    return {
      sliderWidth: 0,
      sliderValues: [],
      sliderValue: null,
      sliderMax: null,
      sliderMin: null
    };
  },
  computed: {
    sliderLabel() {
      return this.sliderValues.length
        ? this.sliderValues[this.sliderValue - 1].label
        : this.sliderValue;
    },
    sliderOutputValue() {
      return this.sliderValues.length
        ? this.sliderValues[this.sliderValue - 1].value
        : this.sliderValue;
    },
    position() {
      const val = this.sliderValue;
      const width = this.sliderWidth - 20;

      const percent =
        (val - this.sliderMin) / (this.sliderMax - this.sliderMin);

      const offset = -2;

      const position = width * percent + offset;

      return `${position}px`;
    }
  },
  created() {
    if (this.values.length) {
      this.sliderValues = this.values;
      this.sliderMin = "1";
      this.sliderMax = this.sliderValues.length;

      let index = 0;
      this.values.map((item, i) => {
        if (item.value === this.value) {
          index = i;
        }
        return true;
      });
      this.sliderValue = index + 1;
    } else {
      this.sliderMin = this.min;
      this.sliderMax = this.max;
      this.sliderValue = this.value;
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.sliderWidth = this.$refs.slider.clientWidth;
    });
  },
  methods: {
    update() {
      this.$emit("input", this.sliderOutputValue);
    },
    change() {
      this.$emit("change", this.sliderOutputValue);
    }
  }
};
</script>
