import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import removeDuplicates from '@/mixins/removeDuplicates'


Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        funds: [],
        searchTerm: '',
        filters: {
            strategies: [],
            simple_name: "",
            retrieval_quotation_days: "0",
            min_application_value: "0",
            max_application_value: "0",
            fund_risk: 12
        }
    },
    mutations: {
        GET_FUNDS(state, payload) {
            state.funds = payload;
        },

        GET_STRATEGY_FILTER(state) {
            const _getStragegies = (strategy) => {
                const mainStrategies = state.funds
                  .filter(item => item.specification.fund_macro_strategy.name === strategy)
                  .map(item => ({
                    isChecked: true,
                    main: item.specification.fund_main_strategy.name
                  }))
            
                return removeDuplicates(mainStrategies, 'main')
            }
            
            const macroStrategies = state.funds.map(item => item.specification.fund_macro_strategy.name)
            
            state.filters.strategies = macroStrategies.reduce((acc, macroStrategy) => {
                acc[macroStrategy] = _getStragegies(macroStrategy)
        
                return acc
              }, {})
        },

        GET_QUOTATION_DAYS(state) {
            const days = state.funds.map(fund => fund.operability.retrieval_quotation_days)
            state.filters.retrieval_quotation_days = String(Math.max(...days))
        },

        GET_APPLICATION_VALUE(state) {
            const value = state.funds.map(fund => fund.operability.minimum_initial_application_amount)
            state.filters.min_application_value = String(Math.min(...value))
            state.filters.max_application_value = String(Math.max(...value))
        }
    },
    actions: {
        fetch_funds({commit}, {app}) {
            app.loading = true;
            axios.get(process.env.VUE_APP_BASE_URL)
            .then(res => {
                commit('GET_FUNDS', res.data);
                commit('GET_STRATEGY_FILTER');
                commit('GET_QUOTATION_DAYS');
                commit('GET_APPLICATION_VALUE');
                app.loading = false;
            })
            .catch((error) => {
                app.loading = false;
                alert('Ocorreu algum erro:', error)
            })
        }
    },
    modules: {
    }
})
