<template>
    <div :class="'cell medium-' + column">
        <div class="app-input">      
            <input type="text" :value="name" @input="updateSelf($event.target.value)">
            <i :class="'icon-' + iconName" v-if="iconName"></i>
            <span class="highlight"></span>
            <span class="bar"></span>
            <label>{{label}}</label>
        </div>
    </div>
</template>

<script>
export default {
    model: {
        prop: "name",
        event: "input"
    },
    props: {
        column: { required: true },
        label: { required: true },
        name: { required: true },
        iconName: { required: false, default: false }
    },
    methods: {
        updateSelf(name) {
            this.$emit("input", name);
        }
    }
}
</script>

<style lang="scss" scoped>
    .app-input {
        [class^="icon-"] {
            position: absolute;
            top: 0;
            right: 0;
            margin: 14px 5px 0 5px;
            font-size: 17px;
            transform: rotate(100deg);
        }
    }
</style>